import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DeletedTeacherComponent } from './deletedTeacher.component';

const routes: Routes = [
    {
        path: '',
        component: DeletedTeacherComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DeletedTeacherRoutingModule {}
